import React, { useState, createElement } from 'react';
import { useSpring, animated } from 'react-spring';
import ReactDOM from "react-dom";
import './Pages.css';
import ReactCardFlip from 'react-card-flip';

const Skills = () => {
    const [isHidden, setIsHidden] = useState(true);
    const [isVisible, setIsVisible] = useState(false);
    const [bounce, setDebounce] = useState(false);

    const [Flipped, setIsFlipped] = useState(false);

    const handleCardClick = () => {
        setIsFlipped(!Flipped);
      };

    const handleClick = (event) => {
        if(!bounce){   
            
            setDebounce(true);
            if (event.target.id == "Skills"  ) {
            
                setIsHidden(false);
                const timer = setTimeout(() => {
                    setIsVisible(true);
                }, 1000);
                
            }
            else if (event.target.id  ) {
        
                setIsVisible(false);
                const timer = setTimeout(() => {
                    setIsHidden(true);
                }, 500);
    
            }
        }
    };


    const fadeOutStyle = {
        opacity: isVisible ? 1 : 0,
        transition: 'opacity 1s ease-out', // Adjust the transition duration and timing function
    };

    document.addEventListener('click', handleClick);

return (
    
    <div class={isHidden ? 'hidden' : ''}>
        <div style={fadeOutStyle}>

            <div class="container" style={{ width: "800" }}>
                <div class="textcontainer">
                    <ReactCardFlip isFlipped={Flipped} flipDirection="vertical">
                        <div className="flip-card" key="front" onClick={handleCardClick}>
                            <div className="flip-card-inner">
                            <div className="flip-card-front">
                                <p>Front</p>
                            </div>
                            </div>
                        </div>

                        <div className="flip-card" key="back" onClick={handleCardClick}>
                            <div className="flip-card-inner">
                            <div className="flip-card-back">
                                <p>Back</p>
                            </div>
                            </div>
                        </div>
                    </ReactCardFlip>

                </div>
            </div>
        </div>
    </div>

);
};


export default Skills;